import React , { useState , useEffect }  from "react"
import Layout from '../components/layout'
import SEO from '../components/seo'
import stl from '../STYLES/AUTH/sign-in-up.module.scss'
import { CREATE_JOB_AD , GET_ALL_JOB_ADDS , GET_CURRENT_USER } from '../queries/index'
import { useMutation } from '@apollo/react-hooks';
import  States from '../components/SELECT_OPTIONS/state' 
import Categories from '../components/SELECT_OPTIONS/category'
import { AlertSuccess , ErrorAlert } from '../helpers/index'
import {withUnAuth} from '../components/ifAuth'

const initialState = {
    title:"",
    _id:"",
    body:"",
    city:"",
    phone:"",
    category:"",
    state:"",
    email:""
}


const CreateJobBody = withUnAuth(({getCurrentUser}) => {

    const [ inState , setState ] = useState(initialState)

    useEffect(() => {
        setState({...inState , _id:getCurrentUser._id})
           // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    const [createJobAd , attrs] = useMutation(CREATE_JOB_AD , {
        variables:{state:inState.state,
                   city:inState.city,
                   title:inState.title,
                   body:inState.body,
                   category:inState.category,
                   email:inState.email ,
                   phone:inState.phone,
                   lang:'en',
                   _id:inState._id,
                    },

       refetchQueries:() => [{query:GET_ALL_JOB_ADDS} , {query:GET_CURRENT_USER}],
        onCompleted:(e) => {
            setState({...initialState , _id:getCurrentUser._id})
            AlertSuccess("job ad was created!")
        },
        onError:(error) => ErrorAlert(error)
  
    })

    const handleCreateJob =  async e => {
        e.preventDefault()
        await createJobAd()
    }

    const setStatValue = e => {
        setState({...inState , [e.target.name]:e.target.value})
    }

    return (
        <div className = {stl.background}>
           <form onSubmit = {(e) => handleCreateJob(e)} className = {stl.form}>
             <h1>Create Job POST</h1>
             <input maxLength = '30' value = {inState.title}  name = 'title' onChange = {(e) => setStatValue(e)} placeholder = "post title"/>
             <textarea value = {inState.body}   onChange = {(e) => setStatValue(e)} name = 'body' cols = '15' rows = '7' placeholder = 'post message' />
             <h4 style = {{marginBottom:"10px"}}>Job Location</h4>
             <States value = {inState.state}  onChange = {(e) => setStatValue(e)} style= {{display:"block" , width:"100%" , margin:"0 auto 10px auto" , padding:"10px"}} name = "state" />
             <input value = {inState.city}  name = 'city' onChange = {(e) => setStatValue(e)} placeholder = "city"/>
             <h4 style = {{marginBottom:"10px"}}>Category</h4>
             <Categories value = {inState.category}  onChange = {(e) => setStatValue(e)}  style= {{display:"block" , width:"100%" , margin:"0 auto" , padding:"10px"}} name = "category"  />
             <h4 style = {{margin:"10px"}}>Contacts</h4>
             <input value = {inState.phone}   onChange = {(e) => setStatValue(e)}  type="text" name="phone"  placeholder="phone number : 888-888-8888" pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}" />
             <input value = {inState.email}   onChange = {(e) => setStatValue(e)}  type="email" name="email"  placeholder = 'contact email address' />
             {attrs.loading && <p>creating post...</p>}
             <button type = 'submit'>POST</button>
           </form>
        </div>
    )
})


const CreateJob = () => (
    <Layout>
        <SEO title = 'create-job' />
        <CreateJobBody />
    </Layout>
)

export default CreateJob
