import React from "react"

const Categories = ({defaultValue , value ,name , style, onChange }) => {
    return (
    <select defaultValue = {defaultValue} value = {value} style = {style} name = {name} onChange = {onChange}>
        <option hidden value={null}>Categories</option>
        <option value = {''} >Categories</option>
       <option value = 'Retail salespersons'>Retail salespersons</option>
       <option value = "Combined food preparation and service workers">Combined food preparation and service workers</option>
       <option value = "Office clerks">Office clerks</option>
       <option value = "Medicals">Medicals</option>
       <option value = "Restaurant">Restaurant</option>
       <option value = "Customer service representatives">Customer service representatives</option>
       <option value = "Janitors">Janitors</option>
       <option value = "Front Desk">Front Desk</option>
       <option value = "Security">Security</option>
       <option value = "Stock clerks and order fillers">Stock clerks and order fillers</option>
       <option value = "Bookkeeping, accounting and auditing clerks">Bookkeeping, accounting and auditing clerks</option>
       <option value = "General managers">General managers</option>
       <option value = "Drivers">Drivers</option>
       <option value = "Teachers">Teachers</option>
       <option value = "Artists">Artists</option>
       <option value = "Moving">Moving</option>
       <option value = "Mechanics">Mechanics</option>
       <option value = "Electricians">Electricians</option>
       <option value = "IT">IT</option>
       <option value = "Web Developer">Web Developer</option>
       <option value = "Other">Other</option>
       </select>
    )
}

export default Categories